import Vue from "vue"
import App from "./App.vue"
import router from "./router"
import store from "./store"
import ElementUI from "element-ui"
import VueLazyload from "vue-lazyload"
import "element-ui/lib/theme-chalk/index.css"
import "./assets/style/index.less"
import "./assets/style/el.less"
import Img from "./assets/script/util/imgUrlUtil"
import _enum from "./assets/script/constant/enum"
Vue.use(ElementUI)
Vue.config.productionTip = false
Vue.prototype.$img = Img
Vue.prototype.$enum = _enum
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require("./assets/image/svg/default-picture.svg"),
  loading: require("./assets/image/svg/default-picture.svg"),
  attempt: 1
})
window.app = new Vue({
  router,
  store,
  render: (h) => h(App)
}).$mount("#app")
